<template>
  <div id="groupNote">
    <head-top title="小组笔记"></head-top>
    <van-pull-refresh v-model="isLoading" @refresh="getDataList" class="pull_fresh">
      <r-image-text ref="imageText" v-if="dataList.length > 0" :data-list="dataList">
      </r-image-text>
      <r-lack-data v-else></r-lack-data>
    </van-pull-refresh>
  </div>
</template>

<script>
import RImageText from "@/components/r-image/r-image-text";
import { ModulePicture } from "@/utils/enum";

export default {
  name: "",
  components: { RImageText },
  props: {},
  data () {
    return {
      isLoading: false,
      dataList: []
    };
  },
  created () {
  },
  mounted () {
    this.$nextTick(() => {
      this.getDataList();
    });
  },
  methods: {
    getDataList () {
      this.isLoading = true;
      this.dataList = [];
      this.$mainHttp.groupNote.getListByGroup({
        ciID: this.$store.getters.courseID,
        groupID: this.$store.getters.groupID
      }).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.sort((a, b) => {
            if (a.CreateTime > b.CreateTime) {
              return -1;
            } else {
              return 1;
            }
          });
          res.Datas.forEach(item => {
            let sub = {
              img: this.$mainHttp.common.picture(
                item.GNContent,
                ModulePicture.groupNote
              ),
              state: item.State,
              title: item.GNTtile,
              time: item.CreateTime,
              id: item.GNID,
              content: item.GNContent
            };

            let index = this.dataList.findIndex(
              k => JSON.stringify(sub) === JSON.stringify(k)
            );
            // 避免重复数据
            if (index === -1) this.dataList.push(sub);
          });
        }
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    }
  },
  watch: {}
};
</script>



